<!-- eslint-disable vuejs-accessibility/label-has-for -->
<!-- eslint-disable max-len -->
<!-- eslint-disable vuejs-accessibility/form-control-has-label -->
<template>
  <div class="cart__block">
    <ul class="cart__orders">
      <li class="cart__order" v-for="item in products" :key="item.productId">
        <h3>{{ item.product.title }}</h3>
        <b>{{ (item.amount * item.product.price) | numberFormat}} ₽</b>
        <span>Артикул: {{ item.product.id }}</span>
      </li>
    </ul>

    <div class="cart__total">
      <p>Доставка: <b>500 ₽</b></p>
      <p>Итого: <b>{{ products.length }}</b> товара на сумму <b>{{ totalTotal() | numberFormat }}</b></p>
    </div>
<slot/>
  </div>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';

export default {
  props: ['products'],
  filters: { numberFormat },
  methods: {
    totalTotal() {
      return this.products.reduce((acc, item) => (item.product.price * item.amount) + acc, 0);
    },
  },
};
</script>
