<!-- eslint-disable vuejs-accessibility/label-has-for -->
<!-- eslint-disable max-len -->
<!-- eslint-disable vuejs-accessibility/form-control-has-label -->
<template>
  <div class="form__counter">
    <button type="button" aria-label="Убрать один товар" @click="demotion">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-minus"></use>
      </svg>
    </button>

    <input type="number" v-model.number="amount">

    <button type="button" aria-label="Добавить один товар" @click="rise">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-plus"></use>
      </svg>
    </button>
  </div>
</template>

<script>
export default {
  props: ['resultAmount'],
  data() {
    return {
      amount: 0,
    };
  },
  methods: {
    demotion() {
      this.amount -= 1;
      if (this.amount <= 0) {
        this.amount = 0;
      }
    },
    rise() {
      this.amount += 1;
    },
  },
  watch: {
    amount() {
      this.$emit('numAmount', this.amount);
    },
  },
  mounted() {
    this.amount = this.resultAmount;
  },
};
</script>
<style>
  input::-webkit-outer-spin-button,
  input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }
</style>
