<!-- eslint-disable vuejs-accessibility/label-has-for -->
<!-- eslint-disable max-len -->
<!-- eslint-disable vuejs-accessibility/form-control-has-label -->
<template>
  <li class="cart__item product">
    <div class="product__pic">
      <img :src="item.product.image" width="120" height="120"
           :alt="item.product.title">
    </div>
    <h3 class="product__title">
      {{ item.product.title }}
    </h3>
    <p class="product__info">
      Объем: <span>128GB</span>
    </p>
    <span class="product__code">
                Артикул: {{ item.product.id }}
      <ProductAmount :resultAmount="amount" @numAmount="productAmount"/>
              </span>
    <b class="product__price">
      {{ (item.amount * item.product.price) | numberFormat}} ₽
    </b>

    <button class="product__del button-del" type="button"
            aria-label="Удалить товар из корзины" @click.prevent="deleteProduct(item.productId)">
      <svg class="cursor" width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import { mapActions } from 'vuex';
import ProductAmount from '@/components/ProductAmount.vue';

export default {
  props: ['item'],
  components: { ProductAmount },
  filters: { numberFormat },
  computed: {
    amount: {
      get() {
        return this.item.amount;
      },
      set(value) {
        this.$store.dispatch('updateCartProductAmount', {
          productId: this.item.productId,
          amount: value,
        });
      },
    },
  },
  methods: {
    ...mapActions(['deleteCartProductAmount']),
    deleteProduct() {
      this.deleteCartProductAmount({ productId: this.item.productId });
    },
    productAmount(sum) {
      this.amount = sum;
    },
  },
};
</script>

<style scoped>
  .cursor {
    cursor: pointer;
  }
</style>
